.section h3{
    font-weight: 500;
}

form.form-container{
    margin: 1rem 0rem;
}

p.source-title{
    text-transform: uppercase;
    font-size: 1.25rem;
    letter-spacing: 0.15rem;
    font-weight: 600;
    margin: 1.25rem;
    padding-bottom: 0.2rem;
    border-bottom: 1px solid rgba(255, 255, 255, 0.12);
    display: inline-block;
}

p.source-title span{
    font-size: 1.75rem;   
}

/* div.flex-container{
    display: flex;
    justify-content: center;
    align-items: center;
} */

.flex-container .input{
    width: 50%;
    margin: 1rem;
}

div.container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 1.75rem;
}

.list{
    display: flex;
    /* justify-content: flex-start;
    align-items: flex-start; */
    flex-direction: column;
    gap: 0rem;
}

div.subsource-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0.5rem 1rem;
    padding: 0.5rem 1rem;
    gap: 0.75rem;
    background-color: rgba(145, 144, 144, 0.12);
    border-radius: 10px;
}

div.subsource-container .index-input .input{
    width: 90%;
    margin: 0.75rem;
}
div.subsource-container .index-input {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 60%;
    gap: 1.5rem;
}

div.popup-footer{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    flex-wrap: wrap;
}

@media (max-width: 600px) {
    div.subsource-container .index-input{
        width: 100%;
    }

    .flex-container .input{
        width: 90%;
    }
}

@media (max-width: 450px) {
    div.subsource-container .index-input p{
        display: none;
    }

    div.subsource-container{
        display: block;
        /* margin: 0.2rem 0.5rem;
        padding: 0.2rem 0.5rem; */
        position: relative;
        /* height: auto; */
        padding: 16px 16px;
    }

    div.subsource-container .delete-icon{
        position: absolute;
        top: -0.3rem;
        right: -0.29rem;
    }
}